.size__control {
  appearance: none;
}

.size__switcher {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 82px;
  height: 52px;

  font-weight: bold;
  font-size: 17px;
  line-height: 18px;

  border: 2px solid $gray;
  cursor: pointer;
}

.size__control:checked + .size__switcher {
  border-color: $light-green;
}

.size__control:checked:hover + .size__switcher {
  border-color: $light-green;
}

.size__control:hover:not(:disabled) + .size__switcher {
  border-color: $dark-gray;
}

.size__control:focus:not(:disabled) + .size__switcher::after {
  content: "";
  position: absolute;
  top: -10px;
  right: -20px;
  bottom: -10px;
  left: -20px;
  z-index: -1;

  display: block;

  outline: 2px solid rgba(3, 102, 214, 0.3);
}

.size__control:active:not(:disabled) + .size__switcher {
  border-color: $light-green;
}

.size__control:disabled + .size__switcher {
  opacity: 0.3;

  cursor: default;
}

@media (min-width: $tablet-width) {
  .size__switcher {
    width: 112px;
  }
}
